<template>
  <div class="home">
      <component :is="currentTabComponent" :val="'valA'"></component>
      <my-input v-model="aaa"></my-input>
      <span @click="getVal">获取</span>
      <span @click="getSignature">获取微信签名</span>
      <input type="file" multiple>
  </div>
</template>

<script>
// @ is an alias to /src
import a from '@/components/a.vue'
import b from '@/components/b.vue'
import c from '@/components/c.vue'
import myInput from '@/components/myInput.vue'
import wx from 'weixin-js-sdk'

export default {
  name: 'home',
  data () {
    return {
      currentTabComponent: 'componentC',
      aaa: '111'
    }
  },
  components: {
    'componentA': a,
    'componentB': b,
    'componentC': c,
    myInput
  },
  methods: {
    getVal() {
      console.log(this.aaa)
    },
    getSignature() {
      let localUrl = location.href.split('#')[0];
      let url = location.href;
      this.$axios({
        method: 'GET',
        url: 'http://172.16.35.103:3000',
        params: {
          localUrl,
          url
        }
      }).then(res => {
        console.log(res)
        alert(JSON.stringify({
          debug: false,
          appId: res.data.appId,
          timestamp: res.data.timestamp,
          nonceStr: res.data.nonceStr,
          signature: res.data.signature,
          jsApiList: ['chooseImage']
        }))
        wx.config({
          debug: false,
          appId: res.data.appId,
          timestamp: res.data.timestamp,
          nonceStr: res.data.nonceStr,
          signature: res.data.signature,
          jsApiList: ['chooseImage']
        });
        wx.ready(function() {
          wx.chooseImage({
            count: 100, // 默认9
            sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
            sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
            success: function (res) {
              console.log(res)
              var localIds = res.localIds; // 返回选定照片的本地ID列表，localId可以作为img标签的src属性显示图片
            }
          });
        });
        wx.error(function(err) {
          alert(JSON.stringify(err))
        });
      }).catch(err => {
        console.log(err)
      })
    }
  }
}
</script>
