<template>
  <div>
    <input type="text" :value="value" @input="handleInput">
  </div>
</template>

<script>
export default {
  name: 'MyInput',
  data() {
    return {
      val: ''
    }
  },
  props: {
    value: {
      type: String,
      default: ''
    }
  },
  methods: {
    handleInput(e) {
      console.log(e)
      this.$emit('input', e.target.value)
    }
  }
}
</script>